'use client';

import { useState, useEffect } from 'react';
import { MultipleChoiceQuestion } from '../multiple-choice-question';
import { EssayQuestion } from '../essay-question';
import { FillBlankQuestion } from '../fill-blank-question';
import { MatchingQuestion } from '../matching-question';
import { CodeQuestion } from '../code-question';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { HelpCircle } from 'lucide-react';

// Định nghĩa kiểu câu hỏi từ schema
export type QuestionType = 
  | 'MULTIPLE_CHOICE' 
  | 'ESSAY' 
  | 'TRUE_FALSE' 
  | 'SHORT_ANSWER' 
  | 'MATCHING' 
  | 'FILL_BLANK' 
  | 'CODE' 
  | 'FILE_UPLOAD';

// Định nghĩa interface cho câu hỏi
export interface Question {
  id: string;
  content: string;
  type: QuestionType;
  options?: Option[];
  metadata?: QuestionMetadata;
  mediaType?: string;
  mediaUrl?: string;
  explanation?: string;
}

// Interface cho lựa chọn trong câu hỏi
export interface Option {
  id: string;
  content: string;
  order: number;
  group?: string;    // For MATCHING questions
  position?: number; // For FILL_BLANK questions
}

// Metadata cho các loại câu hỏi khác nhau
export interface QuestionMetadata {
  // Multiple choice
  allowMultiple?: boolean;
  shuffleOptions?: boolean;
  
  // Essay
  minWords?: number;
  maxWords?: number;
  placeholder?: string;
  richText?: boolean;
  
  // Short answer
  caseSensitive?: boolean;
  
  // Fill-in-the-blank
  text?: string;
  
  // Code
  language?: string;
  initialCode?: string;
  testCases?: string[];
}

// Interface cho câu trả lời
export interface Answer {
  questionId: string;
  selectedOptions: string[];
  textAnswer?: string;
  jsonData?: Record<string, any> | null;
}

interface QuestionRendererProps {
  question: Question;
  answer?: Answer;
  onAnswerChange: (answer: Answer) => void;
}

// Component chính để render câu hỏi dựa trên loại
export default function QuestionRenderer({
  question,
  answer,
  onAnswerChange
}: QuestionRendererProps) {
  // Initiate a blank answer if none exists
  const [currentAnswer, setCurrentAnswer] = useState<Answer>({
    questionId: question.id,
    selectedOptions: [],
    textAnswer: '',
    jsonData: null
  });

  // Update local answer when props change
  useEffect(() => {
    if (answer) {
      setCurrentAnswer(answer);
    } else {
      // Reset answer when question changes and no answer exists
      setCurrentAnswer({
        questionId: question.id,
        selectedOptions: [],
        textAnswer: '',
        jsonData: null
      });
    }
  }, [question.id, answer]);

  // Handle answer change and propagate to parent
  const handleAnswerChange = (updatedAnswer: Partial<Answer>) => {
    const newAnswer = {
      ...currentAnswer,
      ...updatedAnswer
    };
    
    setCurrentAnswer(newAnswer);
    onAnswerChange(newAnswer);
  };

  // Media rendering for questions with attached media
  const renderMedia = () => {
    if (!question.mediaUrl) return null;
    
    switch (question.mediaType) {
      case 'IMAGE':
        return (
          <div className="my-4">
            <img
              src={question.mediaUrl}
              alt="Question illustration"
              className="max-w-full rounded-md"
            />
          </div>
        );
      case 'VIDEO':
        return (
          <div className="my-4">
            <video
              src={question.mediaUrl}
              controls
              className="max-w-full rounded-md"
            />
          </div>
        );
      case 'AUDIO':
        return (
          <div className="my-4">
            <audio
              src={question.mediaUrl}
              controls
              className="w-full"
            />
          </div>
        );
      case 'DOCUMENT':
        return (
          <div className="my-4">
            <a
              href={question.mediaUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-500 hover:underline flex items-center"
            >
              <HelpCircle className="h-4 w-4 mr-2" />
              Xem tài liệu đính kèm
            </a>
          </div>
        );
      default:
        return null;
    }
  };

  // Render different question types
  const renderQuestionByType = () => {
    // Check if question is valid
    if (!question) {
      return (
        <Alert>
          <HelpCircle className="h-4 w-4" />
          <AlertTitle>Lỗi</AlertTitle>
          <AlertDescription>
            Không thể tải thông tin câu hỏi.
          </AlertDescription>
        </Alert>
      );
    }

    // Ensure question.options exists
    const questionWithSafeOptions = {
      ...question,
      options: question.options || [],
      metadata: question.metadata || {}
    };

    switch (question.type) {
      case 'MULTIPLE_CHOICE':
        return (
          <MultipleChoiceQuestion
            question={questionWithSafeOptions}
            selectedOptions={currentAnswer.selectedOptions}
            onChange={(selectedOptions) => 
              handleAnswerChange({ selectedOptions })
            }
          />
        );
      
      case 'TRUE_FALSE':
        // TRUE_FALSE is a special case of MULTIPLE_CHOICE
        return (
          <MultipleChoiceQuestion
            question={{
              ...questionWithSafeOptions,
              type: 'MULTIPLE_CHOICE',
              metadata: { ...questionWithSafeOptions.metadata, allowMultiple: false }
            }}
            selectedOptions={currentAnswer.selectedOptions}
            onChange={(selectedOptions) => 
              handleAnswerChange({ selectedOptions })
            }
          />
        );
      
      case 'ESSAY':
        return (
          <EssayQuestion
            question={questionWithSafeOptions}
            textAnswer={currentAnswer.textAnswer || ''}
            onChange={(textAnswer) => 
              handleAnswerChange({ textAnswer })
            }
          />
        );
      
      case 'SHORT_ANSWER':
        // SHORT_ANSWER is similar to ESSAY but with different styling and validation
        return (
          <EssayQuestion
            question={{
              ...questionWithSafeOptions,
              metadata: {
                ...questionWithSafeOptions.metadata,
                maxWords: questionWithSafeOptions.metadata?.maxWords || 50,
                richText: false
              }
            }}
            textAnswer={currentAnswer.textAnswer || ''}
            onChange={(textAnswer) => 
              handleAnswerChange({ textAnswer })
            }
          />
        );
      
      case 'MATCHING':
        return (
          <MatchingQuestion
            question={questionWithSafeOptions}
            jsonData={currentAnswer.jsonData}
            onChange={(jsonData) => 
              handleAnswerChange({ jsonData })
            }
          />
        );
      
      case 'FILL_BLANK':
        return (
          <FillBlankQuestion
            question={questionWithSafeOptions}
            jsonData={currentAnswer.jsonData}
            onChange={(jsonData) => 
              handleAnswerChange({ jsonData })
            }
          />
        );
      
      case 'CODE':
        return (
          <CodeQuestion
            question={questionWithSafeOptions}
            textAnswer={currentAnswer.textAnswer || ''}
            onChange={(textAnswer) => 
              handleAnswerChange({ textAnswer })
            }
          />
        );
      
      case 'FILE_UPLOAD':
        // Placeholder for file upload - will need actual file upload component
        return (
          <Alert>
            <HelpCircle className="h-4 w-4" />
            <AlertTitle>Chức năng đang phát triển</AlertTitle>
            <AlertDescription>
              Chức năng tải file lên chưa được hỗ trợ trong phiên bản hiện tại.
            </AlertDescription>
          </Alert>
        );
      
      default:
        return (
          <Alert>
            <HelpCircle className="h-4 w-4" />
            <AlertTitle>Loại câu hỏi không được hỗ trợ</AlertTitle>
            <AlertDescription>
              Loại câu hỏi {question.type} hiện không được hỗ trợ.
            </AlertDescription>
          </Alert>
        );
    }
  };

  return (
    <div className="space-y-4">
      {/* Question content */}
      <div className="text-lg font-medium">
        {question.content}
      </div>
      
      {/* Question media if exists */}
      {renderMedia()}
      
      {/* Question UI based on type */}
      {renderQuestionByType()}
    </div>
  );
} 