'use client';

import { useState, useEffect } from 'react';
import { Question } from './components/question-renderer';

interface CodeQuestionProps {
  question: Question;
  textAnswer: string;
  onChange: (code: string) => void;
}

export function CodeQuestion({
  question,
  textAnswer,
  onChange
}: CodeQuestionProps) {
  // Extract data from question metadata
  const language = question.metadata?.language || 'javascript';
  const placeholder = question.metadata?.placeholder || '// Write your code here...';
  const initialCode = question.metadata?.initialCode || '';
  
  // Prioritize textAnswer over initialCode if provided
  const [code, setCode] = useState(textAnswer || initialCode || '');
  
  // Update code when textAnswer changes from parent
  useEffect(() => {
    if (textAnswer !== undefined && textAnswer !== code) {
      setCode(textAnswer);
    }
  }, [textAnswer, code]);

  // Handle text change
  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newCode = e.target.value;
    setCode(newCode);
    onChange(newCode);
  };

  // Get syntax highlighting className based on language
  const getLanguageClass = () => {
    const supportedLanguages = [
      'javascript', 'js', 'typescript', 'ts', 'python', 'py', 
      'java', 'c', 'cpp', 'csharp', 'go', 'rust', 'php', 'ruby'
    ];
    
    const lang = language?.toLowerCase() || 'javascript';
    return supportedLanguages.includes(lang) ? `language-${lang}` : 'language-javascript';
  };

  return (
    <div className="space-y-2">
      <div className="flex justify-between items-center mb-2">
        <div className="text-sm font-medium">
          {language && <span className="px-2 py-1 bg-muted rounded text-muted-foreground">{language}</span>}
        </div>
      </div>
      
      <div className="relative border rounded-md">
        <textarea
          value={code}
          onChange={handleChange}
          className="w-full min-h-[300px] p-4 font-mono text-sm bg-slate-950 text-slate-50 rounded-md focus:outline-none focus:ring-2 focus:ring-primary"
          placeholder={placeholder}
          spellCheck={false}
          autoCapitalize="none"
          autoComplete="off"
          autoCorrect="off"
        />
      </div>
      
      <div className="flex justify-between text-xs text-muted-foreground mt-2">
        <div>
          <span>Lines: {code.split('\n').length}</span>
        </div>
        <div>
          <span>Characters: {code.length}</span>
        </div>
      </div>
    </div>
  );
} 