'use client';

import { useState, useEffect } from "react";
import { Question } from "./components/question-renderer";

interface Option {
  id: string;
  content: string;
  order: number;
  group?: string;
}

interface MatchingQuestionProps {
  question: Question;
  jsonData: Record<string, any> | null | undefined;
  onChange: (matches: Record<string, string>) => void;
}

export function MatchingQuestion({ 
  question, 
  jsonData, 
  onChange 
}: MatchingQuestionProps) {
  // Ensure options exists
  const options = question.options || [];
  
  // Parse the existing answer if available
  const existingAnswer = jsonData as Record<string, string> | null;
  
  const [matches, setMatches] = useState<Record<string, string>>(existingAnswer || {});
  
  // Group options
  const leftOptions = options.filter(opt => opt.group === 'left').sort((a, b) => (a.order || 0) - (b.order || 0));
  const rightOptions = options.filter(opt => opt.group === 'right').sort((a, b) => (a.order || 0) - (b.order || 0));
  
  // Handle selection change
  const handleChange = (leftOptionId: string, rightOptionId: string) => {
    const updatedMatches = {
      ...matches,
      [leftOptionId]: rightOptionId,
    };
    
    setMatches(updatedMatches);
    onChange(updatedMatches);
  };
  
  // Update matches when jsonData changes
  useEffect(() => {
    if (jsonData) {
      setMatches(jsonData as Record<string, string>);
    }
  }, [jsonData]);
  
  return (
    <div className="space-y-6">
      {leftOptions.map(leftOption => (
        <div key={leftOption.id} className="flex flex-col sm:flex-row items-start sm:items-center gap-3">
          <div 
            className="font-medium min-w-[200px] p-2 bg-muted rounded-md" 
            dangerouslySetInnerHTML={{ __html: leftOption.content }} 
          />
          
          <div className="flex items-center">
            <span className="hidden sm:inline">→</span>
          </div>
          
          <select
            value={matches[leftOption.id] || ''}
            onChange={(e) => handleChange(leftOption.id, e.target.value)}
            className="w-full sm:w-auto p-2 border rounded"
          >
            <option value="">Select a match</option>
            {rightOptions.map(rightOption => (
              <option key={rightOption.id} value={rightOption.id}>
                {rightOption.content}
              </option>
            ))}
          </select>
        </div>
      ))}
    </div>
  );
} 