'use client';

import { useState, useEffect } from "react";
import { Question } from './components/question-renderer';

interface Option {
  id: string;
  content: string;
  order: number;
}

interface MultipleChoiceQuestionProps {
  question: Question;
  selectedOptions?: string[];
  onChange: (selectedOptions: string[]) => void;
}

export function MultipleChoiceQuestion({ 
  question,
  selectedOptions = [],
  onChange 
}: MultipleChoiceQuestionProps) {
  // Check if options exist in the question object
  const options = question.options || [];
  
  // Get whether multiple selection is allowed from question metadata
  const allowMultiple = question.metadata?.allowMultiple || false;
  
  // For single selection
  const handleRadioChange = (optionId: string) => {
    onChange([optionId]);
  };
  
  // For multiple selection
  const handleCheckboxChange = (optionId: string) => {
    if (allowMultiple) {
      const newSelection = selectedOptions.includes(optionId)
        ? selectedOptions.filter(id => id !== optionId)
        : [...selectedOptions, optionId];
        
      onChange(newSelection);
    }
  };
  
  // Sort options by order (safely)
  const sortedOptions = [...options].sort((a, b) => (a.order || 0) - (b.order || 0));
  
  if (allowMultiple) {
    // Multiple selection version
    return (
      <div className="space-y-3">
        {sortedOptions.map(option => (
          <div key={option.id} className="flex items-start space-x-2">
            <input 
              type="checkbox" 
              id={option.id} 
              name="answer"
              value={option.id}
              checked={selectedOptions.includes(option.id)}
              onChange={() => handleCheckboxChange(option.id)}
              className="mt-1"
            />
            <label 
              htmlFor={option.id} 
              className="flex-1 cursor-pointer"
              dangerouslySetInnerHTML={{ __html: option.content || '' }}
            />
          </div>
        ))}
        <p className="text-sm text-muted-foreground mt-2">
          Select all that apply.
        </p>
      </div>
    );
  }
  
  // Single selection version
  return (
    <div className="space-y-3">
      {sortedOptions.map(option => (
        <div key={option.id} className="flex items-start space-x-2">
          <input 
            type="radio" 
            id={option.id} 
            name="answer"
            value={option.id}
            checked={selectedOptions.length === 1 && selectedOptions[0] === option.id}
            onChange={() => handleRadioChange(option.id)}
            className="mt-1"
          />
          <label 
            htmlFor={option.id} 
            className="flex-1 cursor-pointer"
            dangerouslySetInnerHTML={{ __html: option.content || '' }}
          />
        </div>
      ))}
    </div>
  );
} 