'use client';

import { useState, useEffect } from "react";
import { Question } from "./components/question-renderer";

interface EssayQuestionProps {
  question: Question;
  textAnswer: string;
  onChange: (text: string) => void;
}

export function EssayQuestion({ 
  question,
  textAnswer,
  onChange 
}: EssayQuestionProps) {
  // Extract metadata from question if available
  const minWords = question.metadata?.minWords;
  const maxWords = question.metadata?.maxWords;
  const placeholder = question.metadata?.placeholder || "Enter your answer here...";
  const richText = question.metadata?.richText || false;
  
  const [text, setText] = useState(textAnswer || '');
  const [wordCount, setWordCount] = useState(0);
  
  // Count words in the text
  useEffect(() => {
    if (text.trim()) {
      const words = text.trim().split(/\s+/);
      setWordCount(words.length);
    } else {
      setWordCount(0);
    }
  }, [text]);
  
  // Handle text change
  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newText = e.target.value;
    setText(newText);
    onChange(newText);
  };
  
  return (
    <div className="space-y-2">
      <textarea
        value={text}
        onChange={handleChange}
        className="w-full min-h-[200px] p-3 border rounded"
        placeholder={placeholder}
      />
      
      <div className="flex justify-between text-sm text-muted-foreground">
        <div>
          {(minWords || maxWords) && (
            <span>
              {minWords && `Minimum words: ${minWords}. `}
              {maxWords && `Maximum words: ${maxWords}. `}
            </span>
          )}
        </div>
        
        <div>
          <span className={`
            ${minWords && wordCount < minWords ? 'text-destructive' : ''}
            ${maxWords && wordCount > maxWords ? 'text-destructive' : ''}
          `}>
            Word count: {wordCount}
          </span>
        </div>
      </div>
    </div>
  );
} 