'use client';

import { useState, useEffect } from "react";
import { Question } from "./components/question-renderer";

interface Option {
  id: string;
  content: string;
  position?: number;
}

interface FillBlankQuestionProps {
  question: Question;
  jsonData: Record<string, any> | null | undefined;
  onChange: (blanks: Record<string, string>) => void;
}

export function FillBlankQuestion({ 
  question, 
  jsonData, 
  onChange 
}: FillBlankQuestionProps) {
  // Get data from question
  const text = question.metadata?.text || "";
  const options = question.options || [];
  const caseSensitive = question.metadata?.caseSensitive || false;
  
  // Parse the existing answer if available
  const existingAnswer = jsonData as Record<string, string> | null;
  
  const [blanks, setBlanks] = useState<Record<string, string>>(existingAnswer || {});
  
  // Parse the text to identify blanks
  const renderText = () => {
    // Using a regular expression to identify blanks in the format [blank_1], [blank_2], etc.
    const blankRegex = /\[blank_(\d+)\]/g;
    
    // Split the text at each blank and create an array of segments
    const segments = [];
    let lastIndex = 0;
    let match;
    
    const tempText = text || '';
    
    while ((match = blankRegex.exec(tempText)) !== null) {
      // Add the text before the blank
      segments.push({
        type: 'text',
        content: tempText.substring(lastIndex, match.index)
      });
      
      // Add the blank
      segments.push({
        type: 'blank',
        position: match[1],
        placeholder: `Blank ${match[1]}`
      });
      
      lastIndex = match.index + match[0].length;
    }
    
    // Add the remaining text
    if (lastIndex < tempText.length) {
      segments.push({
        type: 'text',
        content: tempText.substring(lastIndex)
      });
    }
    
    return segments;
  };
  
  // Handle input change for a blank
  const handleBlankChange = (position: string, value: string) => {
    const updatedBlanks = {
      ...blanks,
      [position]: value,
    };
    
    setBlanks(updatedBlanks);
    onChange(updatedBlanks);
  };
  
  // Update blanks when jsonData changes
  useEffect(() => {
    if (jsonData) {
      setBlanks(jsonData as Record<string, string>);
    }
  }, [jsonData]);
  
  // Render the text with blanks
  const segments = renderText();
  
  return (
    <div className="space-y-4">
      <p className="mb-2">Fill in each blank with the correct answer:</p>
      
      <div className="p-4 bg-muted rounded-md leading-relaxed">
        {segments.map((segment, index) => {
          if (segment.type === 'text') {
            return (
              <span 
                key={index} 
                dangerouslySetInnerHTML={{ __html: segment.content || '' }} 
              />
            );
          } else {
            const position = segment.position as string;
            return (
              <input
                key={index}
                type="text"
                value={blanks[position] || ''}
                onChange={(e) => handleBlankChange(position, e.target.value)}
                className="inline-block mx-1 p-1 border rounded w-32 bg-white dark:bg-gray-800"
                placeholder={segment.placeholder}
                aria-label={`Answer for blank ${position}`}
              />
            );
          }
        })}
      </div>
      
      {caseSensitive && (
        <p className="mt-2 text-sm text-muted-foreground">
          Note: Answers are case-sensitive.
        </p>
      )}
    </div>
  );
} 